function getDate(type) {
	const date = new Date();
	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();

	if (type === 'start') {
		year = year - 60;
	} else if (type === 'end') {
		year = year;
	}
	month = month > 9 ? month : '0' + month;;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
};
export default {
	data() {
		return {
			height: '',
			time: '',
			data: '',
			date: getDate({
				format: true
			}),
			startDate: getDate('start'),
			endDate: getDate('end'),
			order: [],
			merchantId: '',
			tg: true,
			opens: -1,
			height_a: "",
			display: "",
			page: 1,
			orderstart:1,
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.orderDay();
			}
		});
	},
	onReady() {
		this.getTopheight();
	},
	// 上拉加载
	onReachBottom() {
		this.page++;
		if(this.orderstart==1){
			this.orderDay();
		}else{
			this.down_query();
		}
	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
					that.height_a = res.statusBarHeight + 44 + 45 + "px";
				}
			})
		},
		get_time: function(e) {
			this.time = e.target.value
		},
		wz: function(latitude, longitude) {
			uni.openLocation({
				latitude: latitude,
				longitude: longitude,
				success: function(res) {
					console.log('res');
				}
			});
		},
		dh: function(phone) {
			uni.makePhoneCall({
				phoneNumber: phone,
				success: function(res) {
