			uni.showLoading();
			uni.request({
				url: this.serveipd + '/api/merchant/order/completedTime',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
					payTime: that.time,
					page: that.page,
				},
				success: res => {
					uni.hideLoading();
					neworder = res.data.data;
					that.order = that.order.concat(neworder);
					if (that.order.length!=0) {
						that.display = true;
					} else {
						that.display = false;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		down_query: function() {
			let that = this;
